@font-face {
  font-family: "PTMono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.main {
  flex: 1 0 auto;
  background: url("../img/company-background-desktop.png") no-repeat -295px -28px;
}
@media (min-resolution: 2dppx) {
  .main {
    background: url("../img/company-background-desktop@2x.png") no-repeat -295px -28px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: white;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.header {
  color: white;
  background: url("../img/printed-circuit-board-desktop.png") no-repeat 1037px 259px/705px 515px, url("../img/header-figure.svg") no-repeat 82.5% 118px, #1B2D37 url("../img/header-background-desktop.png") no-repeat 0 0/cover;
}
@media (min-resolution: 2dppx) {
  .header {
    background: url("../img/printed-circuit-board-desktop.png") no-repeat 1037px 259px/705px 515px, url("../img/header-figure.svg") no-repeat 82.5% 118px, #1B2D37 url("../img/header-background-desktop@2x.png") no-repeat 0 0/cover;
  }
}
@media (max-width: 1023.99px) {
  .header {
    background: url("../img/printed-circuit-board-tablet.png") no-repeat 100% 112px, url("../img/header-figure.svg") no-repeat 183.5% -22.2%/530px 505px, #1B2D37 url("../img/header-background-tablet.png") no-repeat 0 0/cover;
  }
}
@media (max-width: 1023.99px) and (min-resolution: 2dppx) {
  .header {
    background: url("../img/printed-circuit-board-tablet.png") no-repeat 100% 112px, url("../img/header-figure.svg") no-repeat 183.5% -22.2%/530px 505px, #1B2D37 url("../img/header-background-tablet@2x.png") no-repeat 0 0/cover;
  }
}
@media (max-width: 767.99px) {
  .header {
    background: url("../img/printed-circuit-board-mobile.png") no-repeat 100% 92px, url("../img/header-figure.svg") no-repeat 153px -46px/500px 483px, #1B2D37 url("../img/header-background-mobile.png") no-repeat 0 0/cover;
  }
}
@media (max-width: 767.99px) and (min-resolution: 2dppx) {
  .header {
    background: url("../img/printed-circuit-board-mobile.png") no-repeat 100% 92px, url("../img/header-figure.svg") no-repeat 153px -46px/500px 483px, #1B2D37 url("../img/header-background-mobile@2x.png") no-repeat 0 0/cover;
  }
}

.header__wrapper {
  width: clamp(320px, 100%, 1920px);
  padding: 30px 0 274px;
  margin: 0 auto;
}
@media (max-width: 1023.99px) {
  .header__wrapper {
    padding: 20px 25px 163px;
  }
}
@media (max-width: 767.99px) {
  .header__wrapper {
    min-height: 647px;
    padding: 17px 17px 70px 13px;
  }
}

.header__info {
  width: 100%;
  max-width: 1470px;
  display: flex;
  align-items: center;
  padding: 0 30px;
  margin: 0 auto 229px;
}
@media (max-width: 1023.99px) {
  .header__info {
    padding: 0;
    margin: 0 0 116px;
  }
}
@media (max-width: 767.99px) {
