}

.header__logo-svg {
  width: 202px;
  height: 52px;
  padding: 0;
  margin: 0 auto 0 0;
}
@media (max-width: 767.99px) {
  .header__logo-svg {
    display: none;
  }
}

.header__logo-svg-mobile {
  display: none;
}
@media (max-width: 767.99px) {
  .header__logo-svg-mobile {
    display: inherit;
    width: 42px;
    height: 38px;
    padding: 0 0 0 4px;
