  display: flex;
  justify-content: space-between;
  padding: 20px 0 0;
  margin: 0 42px 20px 0;
  transition-duration: 1s;
}
@media (max-width: 1023.99px) {
  .header__info-email {
    width: max-content;
    padding: 0;
    margin: 0 34px 0 0;
  }
}
@media (max-width: 767.99px) {
  .header__info-email {
    margin: 0 23.33px 0 0;
  }
}
.header__info-email:hover {
  color: deepskyblue;
  transition-duration: 0.5s;
}

.header__info-email svg {
  width: 19px;
  height: 17px;
  margin: 0;
}
@media (max-width: 1023.99px) {
  .header__info-email svg {
    width: 25.33px;
    height: 18.67px;
  }
}
@media (max-width: 767.99px) {
  .header__info-email svg {
    width: 25px;
    height: 19px;
  }
}

.header__info-email path {
  transition-duration: 1s;
}

.header__info-email:hover path {
  fill: #ffffff;
  transition-duration: 0.5s;
}

@media (max-width: 1023.99px) {
