  transition-duration: 0.5s;
}
@media (max-width: 1023.99px) {
  .header__info-phone {
    width: max-content;
    padding: 0;
    margin: 0;
  }
}

.header__info-phone svg {
  width: 18px;
  height: 18px;
}
@media (max-width: 1023.99px) {
  .header__info-phone svg {
    width: 22.54px;
    height: 22.61px;
  }
}

.header__info-phone path {
