    display: none;
  }
}

.header__info > a {
  min-width: 180px;
  min-height: 45px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  line-height: 16.41px;
  font-weight: 700;
  letter-spacing: 5%;
  text-align: center;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 5px;
  padding: 13px 0 8px;
  transition-duration: 1s;
}
@media (max-width: 1023.99px) {
  .header__info > a {
    display: none;
  }
}
.header__info > a:hover {
  box-shadow: 0 0 10px 5px currentColor;
  transition-duration: 0.5s;
}

.header__main {
  width: 74%;
  display: flex;
  margin: 0 auto;
}
@media (max-width: 1023.99px) {
  .header__main {
