}
@media (max-width: 767.99px) {
  .header__main {
    width: 100%;
    flex-flow: column-reverse;
    padding: 0;
    margin: 0;
  }
}

.header__main-info {
  width: 100%;
}
@media (max-width: 1023.99px) {
  .header__main-info {
    width: 100%;
