    margin: 0;
  }
}
@media (max-width: 767.99px) {
  .header__main-info {
    width: 100%;
    padding: 0 0 0 2px;
    margin: 0 auto 0 0;
  }
}

.header__main-info h1 {
  font-size: 80px;
  line-height: 93.75px;
  font-weight: 700;
  letter-spacing: 0.072em;
  text-transform: uppercase;
  display: block;
  margin: 0 0 27px;
}
@media (max-width: 1023.99px) {
  .header__main-info h1 {
    width: 74%;
    font-size: 60px;
    line-height: 70.31px;
    margin: 0 0 28px;
