    top: 17px;
    left: 3px;
  }
}
@media (max-width: 767.99px) {
  .header__main-info p {
    width: 100%;
    font-size: 16px;
    line-height: 25.09px;
    margin: 0 0 41px;
  }
  .header__main-info p::before {
    width: 33px;
    top: 16px;
    left: 3px;
  }
}

.header__main-info-button {
  width: 407px;
  height: 55px;
  font-size: 16px;
