    width: 379px;
    margin: 0 0 0 51px;
  }
}
@media (max-width: 767.99px) {
  .header__main-info-button {
    display: none;
  }
}
.header__main-info-button:hover {
  box-shadow: 0 0 10px 5px currentColor;
  transition-duration: 0.5s;
}

.header__main-info-button-mobile {
  display: none;
}
@media (max-width: 767.99px) {
  .header__main-info-button-mobile {
    width: 287px;
    height: 55px;
    font-size: 16px;
    line-height: 18.75px;
    font-weight: 700;
