.header__main-picture {
  width: 36.72%;
}
@media (max-width: 767.99px) {
  .header__main-picture {
    width: 100%;
    padding: 0 0 0 6px;
    margin: 0 0 4px;
  }
}

.header__main-picture img {
  width: 100%;
  height: 515px;
}
@media (max-width: 1023.99px) {
  .header__main-picture img {
    height: auto;
  }
}
@media (max-width: 767.99px) {
  .header__main-picture img {
    height: 220px;
    display: block;
    margin: 0 auto;
  }
}

.company {
  width: 100%;
  max-width: 1230px;
  padding: 112px 30px 110px;
  margin: 0 auto;
}
@media (max-width: 1023.99px) {
  .company {
    padding: 54px 24px 83px;
  }
}
@media (max-width: 767.99px) {
  .company {
    padding: 36px 10px 66px;
  }
}

.company ul {
  width: 100%;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  position: relative;
  background: white;
  padding: 0;
  margin: 0 0 107px;
}
@media (max-width: 1023.99px) {
  .company ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 306px 232px;
    gap: 30px 28px;
    margin: 0 0 55px;
  }
}
@media (max-width: 767.99px) {
  .company ul {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 310px 310px 232px 244px;
    gap: 20px 0;
    padding: 0 0 0 3px;
    margin: 0 0 58px;
  }
}

.company li {
  width: 100%;
  min-height: 299px;
  position: relative;
  border: 1px solid #EBEBEB;
  padding: 40px 25px 49px 43px;
}
.company li::before {
  content: "";
  width: 2px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #00B2FF;
}
@media (max-width: 1023.99px) {
  .company li {
    width: 100%;
    height: auto;
    min-height: 232px;
    padding: 24px 18px 49px 27px;
  }
}
@media (max-width: 767.99px) {
  .company li {
    padding: 25px 9px 49px 27px;
  }
}

@media (max-width: 767.99px) {
  .company li:nth-child(3n+2) {
    padding: 25px 8px 49px 27px;
  }
}

.company li:nth-child(3n+3) {
  padding: 46px 45px 49px 44px;
}
@media (max-width: 1023.99px) {
  .company li:nth-child(3n+3) {
    padding: 24px 27px 49px;
  }
}

.company svg {
  margin: 0 0 15px;
}
@media (max-width: 1023.99px) {
