}
@media (max-width: 1023.99px) {
  .company li:nth-child(4n+1) svg {
    width: 31px;
    height: 35px;
  }
}

.company li:nth-child(4n+2) svg {
  width: 35px;
  height: 35px;
}
@media (max-width: 1023.99px) {
  .company li:nth-child(4n+2) svg {
    width: 32px;
    height: 31px;
    margin: 0 0 7px;
  }
}
@media (max-width: 767.99px) {
  .company li:nth-child(4n+2) svg {
    margin: 0 0 9px;
  }
}

.company li:nth-child(4n+3) svg {
  width: 35px;
  height: 29px;
}
@media (max-width: 1023.99px) {
  .company li:nth-child(4n+3) svg {
    width: 31px;
    height: 30px;
    margin: 0 0 5px;
  }
}

.company li:nth-child(4n+4) svg {
